
.shopcart {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 48px;
  z-index: 50;
}
.shopcart .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #141d27;
}
.shopcart .content .left {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.shopcart .content .left .logo-wrapper {
  display: inline-block;
  position: relative;
  top: -10px;
  margin: 0 12px;
  width: 56px;
  height: 56px;
  line-height: 56px;
  border-radius: 50%;
  background: #141d27;
  text-align: center;
  font-size: 0;
}
.shopcart .content .left .logo-wrapper .logo {
  display: inline-block;
  margin-top: 6px;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  background: #2b343c;
}
.shopcart .content .left .logo-wrapper .logo.highlight {
  background: #00a0dc;
}
.shopcart .content .left .logo-wrapper .logo .icon-shopping_cart {
  font-size: 24px;
  line-height: 44px;
  color: #80858a;
}
.shopcart .content .left .logo-wrapper .logo .icon-shopping_cart.highlight {
  color: #fff;
}
.shopcart .content .left .logo-wrapper .num {
  position: absolute;
  top: 0;
  right: 0;
  padding: 0 4px;
  height: 16px;
  line-height: 16px;
  text-align: center;
  border-radius: 16px;
  font-size: 9px;
  font-weight: 700;
  color: #fff;
  background: #f01414;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.4);
          box-shadow: 0 4px 8px 0 rgba(0,0,0,0.4);
}
.shopcart .content .left .price {
  vertical-align: top;
  display: inline-block;
  margin-top: 12px;
  margin-left: 15px;
  padding-right: 12px;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
  color: rgba(255,255,255,0.4);
  border-right: 1px solid rgba(255,255,255,0.1);
}
.shopcart .content .left .desc {
  display: inline-block;
  vertical-align: top;
  padding-left: 12px;
  font-size: 12px;
  line-height: 48px;
  font-weight: 200;
  color: rgba(255,255,255,0.4);
}
.shopcart .content .right {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 105px;
          flex: 0 0 105px;
  background: #2b343c;
  font-size: 14px;
  font-weight: 700;
  line-height: 48px;
  color: rgba(255,255,255,0.4);
  text-align: center;
}
.shopcart .content .right .pay.not-enought {
  background: #2b333b;
}
.shopcart .content .right .pay.enought {
  background: #00b43c;
  color: #fff;
}
.shopcart .ball-container span .ball {
  position: fixed;
  z-index: 200;
}
.shopcart .ball-container span .ball .inner {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #00a0dc;
}


